<template>
  <div class="oneseven">
    <move :moveFlag='moveFlag' @changeTrs='changeTrsStyle'>
      <div class="wrap">
        <div class="title">
          <span></span>
        </div>
        <div class="text item">
          <span></span>
        </div>
        <div class="back item">
          <span><b class='key'>position</b><b class='value'>: absolute;</b></span>
          <span><b class='key'>top</b><b class='value'>: 50%;</b>&nbsp;&nbsp;<b class='key'>left</b><b class='value'>: 50%;</b></span>
          <span><b class='key'>transform</b><b class='value'>: translate(-50%, -50%);</b></span>
        </div>
        <div class="text item">
          <span>1</span><br>
          <span>2transform-style: preserve-3d</span>
        </div>
        <div class="backorigin item">
          <span><b class='key'>width</b><b class='value'>: 200px;</b></span>
          <span><b class='key'>padding</b><b class='value'>: 10px 15px;</b></span>
          <span><b class='key'>margin</b><b class='value'>: 50vh auto 0;</b></span>
          <span><b class='key'>transform</b><b class='value'>: translateY(-50%);</b></span>
        </div>
        <div class="text item">
          <span>margin-top50vh50%margin1vh1%</span>
        </div>
      </div>
    </move>
  </div>
</template>

<script>
import move from '@/components/Move'
export default {
  name: 'oneseven',
  created() {
    document.title = "";

  },
  mounted() {

  },
  data() {
    return {
      moveFlag: {
        next: true,
        prev: true,
        nextRoute: '/oneeight',
        prevRoute: '/onesix',
      },
    };
  },
  methods: {
    changeTrsStyle(style) {
      this.$emit('changeTrs', {
        'drc': style.drc,
        "iBig": style.iBig

      });
    },
  },
  components: {
    move
  },

}
</script>
<style lang="scss"  scoped>
.oneseven {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  /* background-color: #E67FA2; */
}
.back {
  background: #fff;
  span {
    display: block;
  }
}
.backorigin {
  background: #fff;
  span {
    display: block;
  }
}
</style>



