<template>
  <div class="onefour">
    <move :moveFlag='moveFlag' @changeTrs='changeTrsStyle'>
      <div class="wrap">
        <div class="title">
          <span></span>
        </div>
        <div class="back item">
          <span><b class='key'>box-shadow</b><b class='value'>: 4px 6px 8px rgba(0,0,0,.5);</b></span>
        </div>
        <div class="text item">
          <span>text-shadowtext-shadow</span>
        </div>
        <div class="backorigin item">
          <span><b class='key'>box-shadow</b><b class='value'>: 0px 10px 8px -8px rgba(0,0,0,.5);</b></span>
        </div>
        <div class="text item">
          <span>-5px10px</span>
        </div>
        <div class="backtwo item">
          <span><b class='key'>box-shadow</b><b class='value'>: 0px 8px 8px -8px rgba(0,0,0,.5),
                      0px -8px 8px -8px rgba(0,0,0,.5);</b></span>
        </div>
        <div class="text item">
          <span>svgfilter</span>
        </div>
      </div>
    </move>
  </div>
</template>

<script>
import move from '@/components/Move'
export default {
  name: 'onefour',
  created() {
    document.title = "";

  },
  mounted() {

  },
  data() {
    return {
      moveFlag: {
        next: true,
        prev: true,
        nextRoute: '/onefive',
        prevRoute: '/onethree',
      },
    };
  },
  methods: {
    changeTrsStyle(style) {
      this.$emit('changeTrs', {
        'drc': style.drc,
        "iBig": style.iBig

      });
    },
  },
  components: {
    move
  },

}
</script>
<style lang="scss"  scoped>
.onefour {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  /* background-color: #E67FA2; */
}
.back {
  background: #fff;
  box-shadow: 4px 6px 8px rgba(0,0,0,.5);
  span {
    display: block;
  }
}
.backorigin {
  background: #fff;
  box-shadow: 0px 10px 8px -8px rgba(0,0,0,.5);
  span {
    display: block;
  }
}
.backtwo {
  background: #fff;
  box-shadow: 0px 8px 8px -8px rgba(0,0,0,.5),
              0px -8px 8px -8px rgba(0,0,0,.5);
  span {
    display: block;
  }
}
</style>



