<template>
<div class="two">
  <move :moveFlag='moveFlag' @changeTrs='changeTrsStyle'>
    <div class="wrap">
      <div class="title">
        <span></span>
      </div>
      <div class="moreBorderNo item">
        <span><b class='key'>background</b><b class='value'>: #fff;</b></span>
        <span><b class='key'>box-shadow</b><b class='value'>: 0 0 0 10px #655;</b></span>
      </div>
      <div class="text item">
        <span>box-shadowborder</span>
      </div>
      <div class="moreBorder item">
        <span><b class='key'>background</b><b class='value'>: #fff;</b></span>
        <span><b class='key'>box-shadow</b><b class='value'>: 0 0 0 10px #655,0 0 0 20px deeppink;</b></span>
      </div>
      <div class="text item">
        <span>box-shadowborder<b style='color: #e60000'></b>()</span>
      </div>
      <div class="moreBorderlast item">
        <span><b class='key'>background</b><b class='value'>: #fff;</b></span>
        <span><b class='key'>box-shadow</b><b class='value'>: 0 0 0 10px #655,0 0 0 20px deeppink;</b></span>
        <span><b class='key'>margin</b><b class='value'>: 10px auto;</b></span>
      </div>
      <div class="text item">
        <span></span>
        <span>borderoutlinebox-shadow</span>
      </div>
    </div>
  </move>
</div>
</template>

<script>
import move from '@/components/Move'
export default {
  name: 'two',
  data() {
    return {
      moveFlag: {
        next: true,
        prev: true,
        nextRoute: '/three',
        prevRoute: '/one',
      },
    }
  },
  created() {
    document.title = "";

  },
  mounted() {

  },
  methods: {
    changeTrsStyle(style) {
      this.$emit('changeTrs', {
        'drc': style.drc,
        "iBig": style.iBig

      });
    },
  },
  components: {
    move
  },

}
</script>
<style lang="scss" scoped>
.two {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  /* background-color: #B5CFD8; */
}
.moreBorderNo {
  background: #fff;
  box-shadow: 0 0 0 10px #655;
  span {
    display: block;
  }
}
.moreBorder {
  background: #fff;
  box-shadow: 0 0 0 10px #655,
              0 0 0 20px deeppink;
  width: calc(80vw - 20px);
  span {
    display: block;
  }
}
.moreBorderlast {
  background: #fff;
  box-shadow: 0 0 0 10px #655,
              0 0 0 20px deeppink;
  width: calc(80vw - 20px);
  margin: calc(1vh + 10px) auto;
  span {
    display: block;
  }
}
</style>



