<template>
  <div class="onenine">
    <move :moveFlag='moveFlag' @changeTrs='changeTrsStyle'>
      <div class="wrap">
        <div class="title">
          <span></span>
        </div>
        <div class="back item">
          <span><b class='key'>padding</b><b class='value'>: 20px;</b></span>
          <span><b class='key'>background</b><b class='value'>: url(../assets/plant.png) no-repeat bottom right #fff;</b></span>
          <span><b class='key'>background-position</b><b class='value'>: right 20px bottom 20px;</b></span>
        </div>
        <div class="text item">
          <span></span>
        </div>
        <div class="backorigin item">
          <span><b class='key'>padding</b><b class='value'>: 20px;</b></span>

          <span><b class='key'>background</b><b class='value'>: url(../assets/plant.png) no-repeat bottom right #fff;</b></span>
          <span><b class='key'>background-origin</b><b class='value'>: content-box;</b></span>
        </div>
        <div class="text item">
          <span>background-positionpadding box()topleftbottomrightbackground-origincontent-box()padding</span>
        </div>
      </div>
    </move>
  </div>
</template>

<script>
import move from '@/components/Move'
export default {
  name: 'onenine',
  created() {
    document.title = "person";

  },
  mounted() {

  },
  data() {
    return {
      moveFlag: {
        next: true,
        prev: true,
        nextRoute: '/twozero',
        prevRoute: '/oneeight',
      },
    };
  },
  methods: {
    changeTrsStyle(style) {
      this.$emit('changeTrs', {
        'drc': style.drc,
        "iBig": style.iBig

      });
    },
  },
  components: {
    move
  },

}
</script>
<style lang="scss"  scoped>
.onenine {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  /* background-color: #E67FA2; */
}
.back {
  padding: 20px;
  background: url(../assets/plant.png) no-repeat bottom right #fff;
  background-position: right 20px bottom 20px;
  background-size: 30px;
  span {
    display: block;
  }
}
.backorigin {
  padding: 20px;
  background: url(../assets/plant.png) no-repeat bottom right #fff;
  background-origin: content-box;
  background-size: 30px;
  span {
    display: block;
  }
}
</style>



