<template>
  <div class="one">
    <move :moveFlag='moveFlag' @changeTrs='changeTrsStyle'>
      <div class="wrap">
        <div class="title">
          <span></span>
        </div>
        <div class="harfBorderNo item">
          <span><b class='key'>border</b><b class='value'>: 10px solid hsla(0,0%,100%,.5);</b></span>
          <span><b class='key'>background</b><b class='value'>: white;</b></span>
        </div>
        <div class="text item">
          <span>background</span>
        </div>
        <div class="harfBorder item">
          <span><b class='key'>border</b><b class='value'>: 10px solid hsla(0,0%,100%,.5);</b></span>
          <span><b class='key'>background</b><b class='value'>: white;</b></span>
          <span><b class='key'>background-clip</b><b class='value'>: padding-box;</b></span>
        </div>
        <div class="text item">
          <span>background-clipborder-boxpadding-box</span>
        </div>
      </div>
    </move>
  </div>
</template>

<script>
import move from '@/components/Move'
export default {
  name: 'one',
  created() {
    document.title = "";
  },
  mounted() {

  },
  data() {
    return {
      moveFlag: {
        next: true,
        prev: true,
        nextRoute: '/two',
        prevRoute: '/home',
      },
    };
  },
  methods: {
    changeTrsStyle(style) {
      this.$emit('changeTrs', {
        'drc': style.drc,
        "iBig": style.iBig
      });
    },
  },
  components: {
    move
  },

}
</script>
<style lang="scss" scoped>
.one {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
}
.harfBorderNo {
  border: 10px solid hsla(0,0%,100%,.5);
  background: white;
  span {
    display: block;
  }
}
.harfBorder {
  border: 10px solid hsla(0,0%,100%,.5);
  background: white;
  background-clip: padding-box;
  span {
    display: block;
  }
}
</style>



