<template>
  <div class="five">
    <move :moveFlag='moveFlag' @changeTrs='changeTrsStyle'>
      <div class="wrap">
        <div class="title">
          <span></span>
        </div>
        <div class="text item">
          <span></span>
        </div>
        <div class="backorigin item">
          <span><b class='key'>background</b><b class='value'>: #fff;</b></span>
          <span><b class='key'>border-radius</b><b class='value'>: 10px;</b></span>
          <span><b class='key'>box-shadow</b><b class='value'>: 0 0 0 8px #eee;</b></span>
          <span><b class='key'>outline</b><b class='value'>: 8px solid #eee;</b></span>
        </div>
        <div class="text item">
          <span>box-shadowoutlineoutlinebox-shadow</span>
        </div>
      </div>
    </move>
  </div>
</template>

<script>
import move from '@/components/Move'
export default {
  name: 'five',
  created() {
    document.title = "";

  },
  mounted() {

  },
  data() {
    return {
      moveFlag: {
        next: true,
        prev: true,
        nextRoute: '/six',
        prevRoute: '/four',
      },
    };
  },
  methods: {
    changeTrsStyle(style) {
      this.$emit('changeTrs', {
        'drc': style.drc,
        "iBig": style.iBig

      });
    },
  },
  components: {
    move
  },

}
</script>
<style lang="scss"  scoped>
.five {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  /* background-color: #E67FA2; */
}

.backorigin {
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 0 0 8px #ddd;
  outline: 8px solid #ddd;
  span {
    display: block;
  }
}
</style>



